function cases() {
    const pacientsSection = document.querySelector('._3');
    let pacientsItems = pacientsSection.querySelectorAll('[data-pacient]');

    pacientsItems.forEach(pacientItem => {
        pacientItem.addEventListener('click', e => {
            console.dir('asd')
            let pacientTmplId = pacientItem.dataset.pacient;
            let pacientTmpl = document.querySelector(`#${pacientTmplId}`);
            let pacientContent = pacientTmpl.content;
            let clonePacientContent = pacientContent.cloneNode(true);
            let currentPacientSections = document.querySelectorAll(`[data-section]`);
            let clonePacientsSectionContent = pacientsSection.innerHTML;

            pacientsSection.innerHTML = '';
            pacientsSection.append(clonePacientContent);
            pacientsSection.classList.add(pacientTmplId);
            currentPacientSections.forEach(currentPacientSection => {
                if (currentPacientSection.dataset.section == pacientTmplId) {
                    currentPacientSection.classList.add('_active');
                    currentPacientSection.classList.add(pacientTmplId);

                    let changeButton = currentPacientSection.querySelector('.change-case');
                    if (changeButton) {
                        changeButton.addEventListener('click', e => {
                            currentPacientSections.forEach(currentPacientSection => {
                                currentPacientSection.classList.remove('_active');
                                currentPacientSection.classList.remove(pacientTmplId);
                                pacientsSection.innerHTML = '';
                                pacientsSection.classList.remove(pacientTmplId);
                                pacientsSection.innerHTML = clonePacientsSectionContent;
                                cases()
                            });
                        });
                    }
                }
            });
        });
    });
}
